.drawing-container,
.text-container {
  margin-top: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
  border: 1px solid lightgray;
  background: white;
  box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.03);
  text-align: center;
}
.sketch {
  padding: 1rem;
  font-size: 1em;
}
.sketch-year {
  padding: 0.5rem;
}
.slider-output {
  padding: 0.2rem;
}

.slider-container,
.checkbox-container,
.btn {
  font-size: 0.8em;
}

.btn {
  margin-top: 0.5rem;
}

.drawing-thumbnail {
  margin-top: 1rem;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
  border: 1px solid lightgray;
  background: white;
  box-shadow: 0 0 0 5px rgba(0, 0, 0, 0.03);
  text-align: center;
}

.drawing-thumbnail .drawing-container {
  margin-top: 1rem;
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
  box-shadow: none;
  border: 0;
}

.text-container {
  text-align: left;
}
